import React, { useState } from 'react';
import { Home, Image, RefreshCw, Calculator, Menu } from 'lucide-react';

const MobileBottomNav = () => {
  const [activeTab, setActiveTab] = useState('home');

  const navItems = [
    {
      id: 'home',
      icon: Home,
      label: 'HOME',
      color: 'text-gray-700'
    },
    {
      id: 'design',
      icon: Image,
      label: 'DESIGN IDEAS',
      color: 'text-gray-700'
    },
    {
      id: 'begin',
      icon: RefreshCw,
      label: "LET'S BEGIN",
      color: 'text-white',
      isSpecial: true
    },
    {
      id: 'estimate',
      icon: Calculator,
      label: 'GET ESTIMATE',
      color: 'text-gray-700'
    },
    {
      id: 'more',
      icon: Menu,
      label: 'MORE',
      color: 'text-gray-700'
    }
  ];

  return (
    <div className="min-h-screen bg-gray-100 flex items-end">
        {/* Bottom Navigation */}
        <div className="fixed bottom-0 left-1/2 transform -translate-x-1/2 w-full max-w-sm">
          <div className="bg-white border-t border-gray-200 px-2 py-3">
            <div className="flex items-center justify-between">
              {navItems.map((item) => {
                const IconComponent = item.icon;
                const isActive = activeTab === item.id;
                
                return (
                  <button
                    key={item.id}
                    onClick={() => setActiveTab(item.id)}
                    className={`flex flex-col items-center justify-center min-w-0 flex-1 py-2 px-1 transition-all duration-300 ${
                      item.isSpecial 
                        ? 'relative' 
                        : isActive 
                          ? 'transform scale-105' 
                          : 'hover:scale-105'
                    }`}
                  >
                    {item.isSpecial ? (
                      // Special circular button for "LET'S BEGIN"
                      <div className="relative mb-1">
                        <div className="w-14 h-14 bg-gradient-to-br from-red-400 to-red-500 rounded-full flex items-center justify-center shadow-lg transform -translate-y-2">
                          <IconComponent 
                            className={`w-6 h-6 ${item.color} ${isActive ? 'animate-pulse' : ''}`} 
                          />
                        </div>
                        {isActive && (
                          <div className="absolute -inset-1 bg-red-400/30 rounded-full animate-ping"></div>
                        )}
                      </div>
                    ) : (
                      // Regular icons
                      <div className="mb-1 relative">
                        <IconComponent 
                          className={`w-6 h-6 transition-colors duration-300 ${
                            isActive 
                              ? 'text-blue-600' 
                              : item.color + ' hover:text-blue-500'
                          }`} 
                        />
                        {isActive && (
                          <div className="absolute -bottom-1 left-1/2 transform -translate-x-1/2 w-1 h-1 bg-blue-600 rounded-full"></div>
                        )}
                      </div>
                    )}
                    
                    <span className={`text-xs font-medium transition-colors duration-300 text-center leading-tight ${
                      item.isSpecial 
                        ? isActive ? 'text-red-600' : 'text-gray-600'
                        : isActive 
                          ? 'text-blue-600' 
                          : 'text-gray-600'
                    }`}>
                      {item.label}
                    </span>
                  </button>
                );
              })}
            </div>
          </div>
        </div>
      </div>
  );
};

export default MobileBottomNav;